import React, { useState, useEffect, useRef } from "react";
import { data } from "./data";
import Styles from "./styles";

function Listapp() {
  const [users, setdata] = useState(data);
  const [addstyle, addpage] = useState(Styles.add);
  const [mainstyle, mainpage] = useState(Styles.mainpage);
  const [editstyle, editpage] = useState(Styles.editpage);
  const [uedity, edity] = useState("");
  const inputref = useRef(null);
  const addref = useRef(null);

  useEffect(() => {
    inputref.current.focus();
    addref.current.focus();
  });

  function add() {
    mainpage({ ...mainstyle, display: "none" });
    addpage({ ...addstyle, display: "block" });
  }

  function canceladd() {
    var ok = window.confirm("Are you sure?");
    if (ok) {
      document.getElementById("name").value = "";
      document.getElementById("last_name").value = "";
      addpage({ ...addstyle, display: "none" });
      mainpage({ ...mainstyle, display: "block" });
    } else {
      //pass
    }
  }

  function saveadd() {
    var i = users.pop();
    users.push(i);
    var newlastname = document.getElementById("last_name").value;
    var newname = document.getElementById("name").value;
    var newuser = { id: i.id + 1, name: newname, last_name: newlastname };
    setdata([...users, newuser]);
    document.getElementById("name").value = "";
    document.getElementById("last_name").value = "";
    addpage({ ...addstyle, display: "none" });
    mainpage({ ...mainstyle, display: "block" });
  }

  function saveedit() {
    for (var contact of users) {
      if (contact.id === uedity.id) {
        var ind = users.indexOf(contact);
        var edited_name = document.getElementById("e_name").value;
        var edited_lastname = document.getElementById("e_last_name").value;
        var newcontact = {
          id: uedity.id,
          name: edited_name,
          last_name: edited_lastname,
        };
        var newusers = users;
        newusers[ind] = newcontact;
        setdata([...newusers]);

        editpage({ display: "none" });
        mainpage({ display: "block" });
      }
    }
  }

  function canceledit() {
    editpage({ display: "none" });
    mainpage({ display: "block" });
  }
  function Editcontact(edit_usr) {
    edity(edit_usr);
    mainpage({ display: "none" });
    editpage({ display: "block" });
    var name_inp = document.getElementById("e_name");
    var last_name_inp = document.getElementById("e_last_name");
    name_inp.value = edit_usr.name;
    last_name_inp.value = edit_usr.last_name;
  }

  function Deletecontact(del_id) {
    var ok = window.confirm("Are you sure you want to do that?");
    if (ok) {
      console.log(del_id);
      setdata(users.filter((user) => user.id !== del_id));
    } else {
      //pass
    }
  }

  return (
    <div className="container">
      <div className="listapp" style={({}, mainstyle)}>
        <h1 className="header">Contacts</h1>
        <div className="second_frame">
          {users.map((user) => {
            return (
              <section className="contact" key={user.id}>
                <div className="c_names">
                  <h1>{user.id}.</h1>
                  <h2 className="cname">{user.name}</h2>
                  <h2 className="clast_name">{user.last_name}</h2>
                </div>
                <div className="c_buttons">
                  <button className="btn" onClick={() => Editcontact(user)}>
                    Edit
                  </button>
                  <button
                    className="btn"
                    onClick={() => Deletecontact(user.id)}
                  >
                    Delete
                  </button>
                </div>
              </section>
            );
          })}
        </div>
        <button className="btn" onClick={add}>
          {" "}
          Add
        </button>
      </div>

      <div className="add" style={({}, addstyle)}>
        <section>
          <label>Name:</label>
          <input type="text" className="inp" id="name" ref={addref} />
        </section>
        <section>
          <label> Last name:</label>
          <input type="text" className="inp" id="last_name" />
        </section>
        <button type="button" className="btn" onClick={canceladd}>
          Cancel
        </button>
        <button type="button" className="btn" onClick={saveadd}>
          Save
        </button>
      </div>

      <div className="add" style={({}, editstyle)}>
        <section>
          <label>Name:</label>
          <input type="text" className="inp" id="e_name" ref={inputref} />
        </section>
        <section>
          <label>Last name:</label>
          <input type="text" className="inp" id="e_last_name" />
        </section>
        <button type="button" className="btn" onClick={canceledit}>
          Cancel
        </button>
        <button type="button" className="btn" onClick={saveedit}>
          Save
        </button>
      </div>
    </div>
  );
}

export default Listapp;
